import React from 'react'
import styles from '../utils/style.module.css'
import {dominicPAO} from '../data/dominicArray'
const pivalue = ""

class GeneratePAForPi extends React.Component {
  _gettingObjectByNumber = (arr, number) => {
    return arr.find(item => item.number == number)
  }
  _generatePA = () => {
    const {arr, fourDigit, index} = this.props;
    const personNumber = fourDigit.substr(0, 2);
    const actionNumber = fourDigit.substr(2, 4);
    const person = this._gettingObjectByNumber(arr, personNumber);
    const action = this._gettingObjectByNumber(arr, actionNumber);
    const personText = person.person;
    const actionText = action.fullAction;
    return `#${index + 1} - - ${fourDigit} - ${personText}, ${actionText}`;
  }
  render () {
    return (
        <li className={styles.singleList}>{this._generatePA()}</li>
      )
  }
}



class pi extends React.Component {
  _fourDigitPiSplit = () => {
    var piArr = []
    var tempPi = ""
    for (var i = 0; i < pivalue.length; i++) {
      let value = pivalue.charAt(i)
      tempPi = `${tempPi}${value}`
      if (tempPi.length === 4) {
        piArr.push(tempPi)
        tempPi = ``;
      }
    }
    return piArr;
  }
  _generatePAForPi = () => {
    let piArr = this._fourDigitPiSplit()
    let piPA = piArr.map((fourDigit, index) => <GeneratePAForPi index={index} arr={dominicPAO} key={index} fourDigit={fourDigit} />)
    return piPA
  }
  render () {
    return (
        <div>
          <div>
            <h1>pi vlaue without split (4400)</h1>
            <p>{pivalue}</p>
            <h1>pi value with 4 digit split</h1>
            <p>
              {
                this._fourDigitPiSplit().map((digit, index) => <span style={{display: 'inline-block', border: '1px solid #444', padding: '5px 10px', margin: 5, }} key={index}>{digit}</span>)
              }
            </p>
          </div>
          <h1> pi value dominic PA </h1>
          <ol className={styles.uList}>
            {this._generatePAForPi()}
          </ol>
        </div>
      )
  }
}

export default pi


